.blog {
  max-width: 100%;
  width: 100%;

  &_links {
    color: $color_grey-dark;
    font-size: $font_size-12;
    margin-bottom: 2rem;

    @media only screen and (min-width: 1104px) {
      font-size: $font_size-16;
    }
  }

  &_links_item {
    text-decoration: none;
    color: $color_grey-dark;
  }

  &_btn {
    text-transform: uppercase;
    font-weight: $font_weight-bold;
    margin-bottom: 2rem;
  }

  &_info {
    @media only screen and (min-width: 400px) {
      display: flex;
      justify-content: space-between;
    }
  }

  &_title {
    font-family: 'AmsiProNarw-BlackItalic';
    font-size: $font_size-30;
    text-align: center;
    margin-bottom: 2rem;

    @media only screen and (min-width: 1104px) {
      font-size: $font_size-37;
    }
  }

  &_img {
    width: 100%;
    margin-bottom: 2rem;
  }

  &_subtitle {
    text-transform: uppercase;
    font-weight: $font_weight-bold;
    line-height: 1.5;
    margin-bottom: 2rem;
  }

  @media only screen and (min-width: 768px) {
    max-width: 768px;
  }

  @media only screen and (min-width: 1104px) {
    max-width: 1104px;
  }

  @media only screen and (min-width: 1301px) {
    max-width: 818px;
  }
}

.paragraph {
  &_pink-text {
    color: $color_pink-main;
  
    &-bold {
      color: $color_pink-main;
      font-weight: $font_weight-bold;
    }
  }

  &_text-bold {
    font-weight: $font_weight-bold;
  }

  &_text-italic {
    font-style: italic;
  }
}

.quotation {
  display: flex;
  margin-bottom: 2rem;
}

.quotation_img {
  width: 2rem;
  align-self: flex-start;
  margin-right: 1rem;

  @media only screen and (min-width: 1104px) {
    width: 4rem;
  }
}

.quotation_img:last-child {
  align-self: flex-end;
  transform: rotate(180deg);
}

.quotation > .paragraph_text {
  margin-bottom: 0;
}

